    opacity: 1;
  }
  20% {
    transform: translateY(0);
    opacity: 1;
  }
  40% {
    transform: translateY(0);
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  60% {
    transform: translateY(calc(-1 * var(--perDistance)));
    opacity: 1;
  }

  80% {
    transform: translateY(calc(-1 * var(--perDistance)));
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
