          .span1 {
            font-size: 15px !important;
          }
          .plus {
            font-size: 15px !important;
          }
          .span2 {
            font-size: 10px !important;
          }
        }
      }
      .trans6 {
        .trans6Text {
          margin-right: 30px !important;
          img {
            width: 30px !important;
          }
        }
      }
      .trans7 {
        .textCol {
          margin-left: 40px !important;
        }
      }
    }
  }
  @keyframes visiblespan {
    0% {
      opacity: 1 !important;
    }
